<template lang="pug">
    .table(:class="loaderClass")
        .preloader__spinner
            .preloader__animation
        .table__item
            .table__heading <i class="fas fa-female"></i> Кандидатки ({{ aspirantNumber }})
            .table__content
                eva(v-for="eva in evas", v-if="eva.status=='aspirant'", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading <i class="fas fa-users"></i> Подруги ({{ friendNumber }})
            .table__content
                eva(v-for="eva in evas", v-if="eva.status=='friend'", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading <i class="fas fa-venus-mars"></i> Любовницы ({{ mistressNumber }})
            .table__content
                eva(v-for="eva in evas", v-if="eva.status=='mistress'", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading <i class="fas fa-praying-hands"></i> Избранные ({{ favoriteNumber }})
            .table__content
                eva(v-for="eva in evas", v-if="eva.status=='favorite'",  :winners="winnerNumber", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading <i class="fas fa-hand-holding-heart"></i> Победитель (всегда одна)
            .table__content
                eva(v-for="eva in evas", v-if="eva.status=='winner'", :eva="eva", v-bind:key="eva.id")
</template>

<script>
    import eva from './eva';
    import { mapState, mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    export default {
      name: 'app_table',
      data(){
          return {
            loaderClass: 'table--loading'
          }
      },
      computed: {
        ...mapState({
            evas: state => state.evas.evas
        }),
        aspirantNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'aspirant'){
                    result++;
                }
            }
            return result;
        },
        friendNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'friend'){
                    result++;
                }
            }
            return result;
        },
        mistressNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'mistress'){
                    result++;
                }
            }
            return result;
        },
        favoriteNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'favorite'){
                    result++;
                }
            }
            return result;
        },
        winnerNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'winner'){
                    result++;
                }
            }
            return result;
        }
      },
      methods: {
         ...mapActions(['getAllEvasFromApi'])
      },
      mounted(){
        this.$nextTick(function () {
            this.getAllEvasFromApi();
        });
        eventBus.$on('loadPage', data => {
          this.loaderClass = data.loaderClass;
        }); 
      },
      components: {
        eva
      }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .table{
        display: grid;
        width: 100%;
        grid-template-columns: repeat(5, 1fr);
        border-radius: 4px;
        background-color: $mainColor;
        transition: all .4s;
        .preloader__spinner{
            display: none;
        }
    }

    .table--loading{
        background-color: transparent;
        .preloader__spinner{
            display: block;
        }
        .table__item{
            opacity:0;
            z-index:-2;
        }
    } 

    .table__item{
        border:2px solid white;
        border-right: none;
        height: 90vh;
        position: relative;
        &:last-of-type{
            border-right:2px solid white;
        }
    }

    .table__content{
        height: calc(100% - 3.44rem);
        overflow-y: auto;
        margin-top: 55px;
    }

    .table__heading {
        background-color: $accentColor;
        height: 50px;
        text-align: center;
        line-height: 50px;
        font-size: $h5fontSize;
        margin-bottom: 20px;
        position: absolute;
        width: 100%;
        color: $mainColor;
    }
</style>
<style lang="scss" src="../assets/styles/elements/preloader.scss"></style>


