<template lang="pug">
    include ../templates/button  
    .winner-eva(@click="openWinnerWindow")
        +button("winner", "inWinner", "Выбрать", "fa-hand-holding-heart")
</template>

<script>
    import {eventBus} from '../main.js';
    export default {
        name: 'winner-btn',
        props: {
          eva: Object,
          winners: Number
        },
        methods: {
          openWinnerWindow() {
              eventBus.$emit('callWinnerWindow', {visible: true, eva: this.eva, winnNumber: this.winners});
          }
        }
    }
</script>

<style lang="scss" src="../assets/styles/elements/button.scss"></style>


