<template lang="pug">
    .winner-window.window-archive(v-if="visible")
       .window-archive__overlay(@click="closeThisWindow")
       .window-archive__content 
        .window-archive__title.winner-window__title Объявить эту Еву ({{ eva.id }}) победильницей?
        .window-archive__subtitle.winner-window__subtitle <i class="fas fa-chess-queen"></i> {{ eva.name }} <i class="fas fa-chess-queen"></i>
        img.window-archive__image.winner-window__image(:src="eva.image") 
        .winner-window__text 
            |Задай себе три вопроса: 
            br
            br
            |1. Вам вдвоем комфортно в быть вместе, как ни с кем другим?
            br
            br
            |2. Вы доверяете друг другу, как никому?
            br
            br
            |3. Вы ОБА активно проявляете внимание и заботу к друг другу, и ОБА  хотите сблизиться? 
            br
            br
            |Если все ответы да, то эту еву можно объявить победительницей, если хотябы один нет, то не стоит торопиться.
        .window-archive__button-set
            button.window-archive__btn(@click="finishGame") да   
            button.window-archive__btn.window-archive__btn--close(@click="closeThisWindow") нет   
</template>

<script>
    import {eventBus} from '../main.js';
    import { mapActions } from 'vuex';
    export default {
      name: 'windowWinn',
      data(){
          return {
              visible: false,
              eva: {},
              winnNumber: 0
          }
      },
      methods: {
          ...mapActions(['endGame']),
          closeThisWindow(){
              this.visible = false;
          }, 
          finishGame(){
              if(this.winnNumber > 0){
                eventBus.$emit('showStatus', {text: 'Той самой может стать только одна, и на данный момент она уже известна!', visible: true, class: 'm-fail'});
                this.closeThisWindow();
                setTimeout(() => {
                    eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                }, 5000);
              } else {
                var eva = this.eva;
                this.$nextTick(function () {
                    this.endGame(eva);
                });  
              }
              
          }
      },
      mounted(){
        eventBus.$on('callWinnerWindow', data => {
          this.visible = data.visible;
          this.eva = data.eva;
          this.winnNumber = data.winnNumber;
        });
      }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss'; 
    @keyframes shadow {
        from {box-shadow: 0 0px 40px 5px #D0FF00;}
        to {box-shadow: 0 0px 80px 40px $accentColor;}
    }
    .window-archive{
        position: fixed;
        width: 100vw;
        height: 100vh;
        z-index: 9000;
        top:0;
        left:0;
        right:0;
        bottom:0;
        display:flex;
    }
    .window-archive__overlay{
        width:100%;
        height: 100%;
        background-color: rgba($mainColor, 0.8);
        position:absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
    }
    .window-archive__content{
        width:40%;
        max-height: 100%;
        overflow:auto;
        border-radius: 4px;
        background-color: $white;
        padding: 35px 40px;
        margin: auto;
        position:relative;
    }

    .close-icon{
        position:absolute;
        top: 20px;
        right: 20px;
        width:20px;
        height:20px;
        cursor:pointer;
        &:hover{
            fill: $accentColor;
        }
    }
    .window-archive__title{
        color: $mainColor;
        font-family: $mainFont;
        font-size: $h2fontSize;
        font-weight: 700;
        line-height: 1.2em;
        text-align:center;
        margin-bottom:20px;
    }
    .winner-window__title{
        color:$accentColor;
    }
    .window-archive__subtitle{
        color: $mainColor;
        font-family: $mainFont;
        font-size: $h2fontSize;
        font-weight: 700;
        line-height: 1.2em;
        text-align:center;
        margin-bottom:20px;
    }
    .winner-window__subtitle{
        color: $accentColor;
    }
    .window-archive__image{
        max-width:150px;
        height: auto;
        display:block;
        margin:0px auto 50px;
        border-radius:50%;
    }
    .winner-window__image{
        animation-name: shadow;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        animation-direction: alternate;
    }
    .window-archive__button-set{
        width:60%;
        display:flex;
        margin:0 auto;
        justify-content: space-between;
    }
    .window-archive__btn{
        height: 50px;
        border-radius: 4px;
        background-color: $accentColor;
        border: 2px solid $accentColor;
        padding:0 50px;
        color: $white;
        font-size: $h6fontSize;
        font-weight: 600;
        letter-spacing: 2px;
        text-transform: uppercase;
        transition: all .3s;
        &:hover{
            background-color:transparent;
            color: $accentColor;
        }
    }
    .window-archive__btn--close{
        background-color: $mainColor;
        border: 2px solid $mainColor;
        &:hover{
            background-color: $accentColor;
            border: 2px solid $accentColor;
            color:white;
        }
    }
    .winner-window__text{
        font-size: $h4fontSize;
        color: red;
        text-align: center;
        font-weight: 700;
        margin-bottom: 20px;
    }
</style>


