<template lang="pug">
    .progress(:class="loaderClass")
        .preloader__spinner
            .preloader__animation
        .progress__title(v-if="loaderClass == ''") Все Евы, за все время: {{ progress.all }}
        .progress__item
            .progress__procent(v-if="friendsProcent > 0") {{ friendsProcent }}%
            .progress__bar(:style="'height:' +  friendsProcent + '%;'") {{ progress.friends.length }}
            .progress__name Подруги 
        .progress__item
            .progress__procent(v-if="mistressesProcent > 0") {{ mistressesProcent }}%
            .progress__bar(:style="'height:' +  mistressesProcent + '%;'") {{ progress.mistresses.length }}
            .progress__name Любовницы 
        .progress__item
            .progress__procent(v-if="favoriteProcent > 0") {{ favoriteProcent }}%
            .progress__bar(:style="'height:' +  favoriteProcent + '%;'") {{ progress.favorites.length }}
            .progress__name Избранные
        .progress__item
            .progress__procent(v-if="winnersProcent > 0") {{ winnersProcent }}%
            .progress__bar(:style="'height:' +  winnersProcent + '%;'") {{ progress.winners.length }}
            .progress__name Победители
        .progress__item
            .progress__procent(v-if="darkProcent > 0") {{ darkProcent }}%
            .progress__bar(:style="'height:' +  darkProcent + '%;'") {{ progress.dark_evas.length  }}
            .progress__name Архив
</template>

<script>
    import { mapState, mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    export default {
      name: 'app_progress',
      data(){
          return {
            loaderClass: 'progress--loading'
          }
      },
      computed: {
        ...mapState({
            progress: state => state.progress
        }),
        procent(){
            var result = this.progress.all / 100;
            if(result <= 0){
                return 0;
            } else {
                return result;
            }
        },
        friendsProcent(){
            var result = (this.progress.friends.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        mistressesProcent(){
            var result = (this.progress.mistresses.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        favoriteProcent(){
            var result = (this.progress.favorites.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        winnersProcent(){
            var result = (this.progress.winners.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        darkProcent(){
            var result = (this.progress.dark_evas.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
      },
      methods: {
         ...mapActions(['getProgressFromApi'])
      },
      mounted(){
        this.$nextTick(function () {
            this.getProgressFromApi();
        });
        eventBus.$on('loadPage', data => {
          this.loaderClass = data.loaderClass;
        });  
      },
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .progress{
        width: 100%;
        height: 90vh;
        border-left: 2px solid $mainColor;
        border-bottom: 2px solid $mainColor;
        position: relative;
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-gap: 150px;
        margin-bottom:20px;
         .preloader__spinner{
            display: none;
        }
    }

    .progress--loading{
        background-color: transparent;
        .preloader__spinner{
            display: block;
        }
        .progress__item{
            opacity:0;
            z-index:-2;
        }
    } 

    .progress__title{
        color: $mainColor;
        font-family: $mainFont;
        font-size: $h3fontSize;
        font-weight: 700;
        position: absolute;
        top:50px;
        left:50px;
    }

    .progress__item{
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        margin-bottom: -24px;
    }

    .progress__bar{
        background-color: $accentColor;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        color: $mainColor;
        font-family: $mainFont;
        font-size: $h3fontSize;
        font-weight: 700;
        padding-bottom: 5px;
        width: 100%;
    }

    .progress__name{
        width: 100%;
        text-align: center;
        color: $mainColor;
        font-family: $mainFont;
        font-size: $h6fontSize;
        font-weight: 700;
    }

    .progress__procent{
        width: 100%;
        text-align: center;
        font-size: $h5fontSize;
        font-weight: 700;
    }
</style>
<style lang="scss" src="../assets/styles/elements/preloader.scss"></style>


