<template lang="pug">
    .table.table--light(:class="loaderClass")
        .preloader__spinner
            .preloader__animation
        .table__item
            .table__heading <i class="fas fa-archive"></i> Архив ({{ progress.dark_evas.length  }})
            .table__content
                past_eva(v-for="eva in progress.dark_evas", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading <i class="fas fa-users"></i> Подруги ({{ progress.friends.length }})
            .table__content
                past_eva(v-for="eva in progress.friends", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading <i class="fas fa-venus-mars"></i> Любовницы ({{ progress.mistresses.length }})
            .table__content
                past_eva(v-for="eva in progress.mistresses", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading <i class="fas fa-praying-hands"></i> Избранные ({{ progress.favorites.length }})
            .table__content
                past_eva(v-for="eva in progress.favorites", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading <i class="fas fa-hand-holding-heart"></i> Победители ({{ progress.winners.length }})
            .table__content
                past_eva(v-for="eva in progress.winners", :eva="eva", v-bind:key="eva.id")
</template>

<script>
    import past_eva from './past-eva';
    import { mapState, mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    export default {
      name: 'app_progress',
      data(){
          return {
            loaderClass: 'table--loading'
          }
      },
      computed: {
        ...mapState({
            progress: state => state.progress
        }),
        procent(){
            var result = this.progress.all / 100;
            if(result <= 0){
                return 0;
            } else {
                return result;
            }
        },
        friendsProcent(){
            var result = (this.progress.friends.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        mistressesProcent(){
            var result = (this.progress.mistresses.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        favoriteProcent(){
            var result = (this.progress.favorites.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        winnersProcent(){
            var result = (this.progress.winners.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
        darkProcent(){
            var result = (this.progress.dark_evas.length / this.procent).toFixed(2);
            if(isNaN(result)){
                return 0;
            } else {
                return result;
            }
        },
      },
      methods: {
         ...mapActions(['getProgressFromApi'])
      },
      mounted(){
        this.$nextTick(function () {
            this.getProgressFromApi();
        });
        eventBus.$on('loadPage', data => {
          this.loaderClass = data.loaderClass;
        });  
      },
      components: {
        past_eva
      }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .table{
        display: grid;
        width: 100%;
        grid-template-columns: repeat(5, 1fr);
        border-radius: 4px;
        transition: all .4s;
        .preloader__spinner{
            display: none;
        }
        &.table--light{
            background-color: #ebebeb;
        }
    }

    .table--loading{
        background-color: transparent;
        .preloader__spinner{
            display: block;
        }
        .table__item{
            opacity:0;
            z-index:-2;
        }
    } 

    .table__item{
        border:2px solid white;
        border-right: none;
        height: 90vh;
        position: relative;
        &:last-of-type{
            border-right:2px solid white;
        }
    }

    .table__content{
        height: calc(100% - 3.44rem);
        overflow-y: auto;
        margin-top: 55px;
    }

    .table__heading {
        background-color: $accentColor;
        height: 50px;
        text-align: center;
        line-height: 50px;
        font-size: $h5fontSize;
        margin-bottom: 20px;
        position: absolute;
        width: 100%;
        color: $mainColor;
    }
</style>
<style lang="scss" src="../assets/styles/elements/preloader.scss"></style>


