import axios from 'axios';
import {eventBus} from '../../main.js';
const evas = {
    state: {
        evas: [
            {
                status: "favorite",
                id:'eva01',
                name: "Вита",
                isActive: true,
                image: "https://via.placeholder.com/300",
                appearance: "4",
                mind: "5",
                penalty: "0",
                text: "Вита Lorem ipsum dolor sit amet consectetur, adipisicing elit. Ad at atque aliquid ex libero quis, quas magni, quo nulla nobis sint. Culpa consequatur hic laboriosam officia voluptate rem illo saepe."
            }
        ]
    },
    mutations: {
        addEvas(state, data){
            state.evas = data;
        }
    },
    actions: {
        getAllEvasFromApi(store){
            axios
                .get('http://localhost:3030/evas')
                .then(response => {
                    store.commit('addEvas', response.data || []);
                })
                .catch(error => {
                    store.commit('addEvas', []);
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'})
                })
                .then(() => {
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''})
                    }, 3000);
                });
        },
        postNewEva(store, eva){
            var data = new FormData();
            if(eva.file){
                data.append('file', eva.file);
            }
            data.append('name', eva.name);
            data.append('status', eva.status);
            data.append('isActive', eva.isActive);
            data.append('appearance', eva.appearance);
            data.append('mind', eva.mind);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post('http://localhost:3030/eva', data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Новая ева добавлена', visible: true, class: ''});
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('openingEvaform', {visible: false});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3000);
                });
        },
        updateEva(store, eva){
            var data = new FormData();
            if(eva.file){
                data.append('file', eva.file);
            }
            data.append('name', eva.name);
            data.append('id', eva.id);
            data.append('status', eva.status);
            data.append('isActive', eva.isActive);
            data.append('appearance', eva.appearance);
            data.append('mind', eva.mind);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post(`http://localhost:3030/updateEva`, data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Данные евы изменены', visible: true, class: ''})
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('openingEvaform', {visible: false});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3000);
                });
        },
        archiveEva(store, eva){
            var data = new FormData();
            data.append('name', eva.name);
            data.append('id', eva.id);
            data.append('status', eva.status);
            data.append('isActive', eva.isActive);
            data.append('appearance', eva.appearance);
            data.append('mind', eva.mind);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post('http://localhost:3030/archive', data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Ева добавленна в архив', visible: true, class: 'm-fail'})
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('callArchiveWindow', {visible: false});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3000);
                });
        },
        strikeEva(store, eva){
            var data = new FormData();
            data.append('penalty', eva.penaltyNumber);
            data.append('id', eva.id);
            axios
                .post('http://localhost:3030/penalty', data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Данные о штрафах евы изменены', visible: true, class: ''})
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('callArchiveWindow', {visible: false});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3000);
                }); 
        },
        moveEva(store, eva){
            var data = new FormData();
            data.append('name', eva.name);
            data.append('id', eva.id);
            data.append('move', eva.move);
            data.append('isActive', eva.isActive);
            data.append('appearance', eva.appearance);
            data.append('mind', eva.mind);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post("http://localhost:3030/move/", data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Данные о статусе евы изменены', visible: true, class: ''});
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('callArchiveWindow', {visible: false});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3000);
                }); 
        },
        endGame(store, eva){
            var data = new FormData();
            data.append('name', eva.name);
            data.append('id', eva.id);
            data.append('move', eva.move);
            data.append('isActive', eva.isActive);
            data.append('appearance', eva.appearance);
            data.append('mind', eva.mind);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post("http://localhost:3030/winner", data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Поздравляю, Павел! Ты нашел ее!', visible: true, class: ''})
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('callWinnerWindow', {visible: false});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 5000);
                });
        }
    }
}

export default evas;


