import axios from 'axios';
import {eventBus} from '../../main.js';
const progress = {
    state: {all:0, friends: [], mistresses: [], favorites: [], dark_evas: [], winners: []},
    mutations: {
        addProgress(state, data){
            state.all = data.all;
            state.friends = data.friends;
            state.mistresses = data.mistresses;
            state.favorites = data.favorites;
            state.dark_evas = data.dark_evas;
            state.winners = data.winners;
        }
    },
    actions: {
        getProgressFromApi(store){
            axios
                .get('http://localhost:3030/progress')
                .then(response => {
                    store.commit('addProgress', response.data);
                })
                .catch(error => {
                    store.commit('addProgress', {all:0, friends: [], mistresses: [], favorites: [], dark_evas: [], winners: []});
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'})
                })
                .then(() => {
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''})
                    }, 3000);
                });
        }
    }
}

export default progress;


