<template lang="pug">
    ul.move
        li.move__item.move__opener <i class="fas fa-layer-group"></i> Поместить...
            ul.move__menu
                li(@click="setStatus", data-status="mistress", v-if="eva.status!='mistress' || !eva.isActive").move__item <i class="fas fa-venus-mars"></i> В любовницы
                li(@click="setStatus", data-status="friend", v-if="eva.status!='friend' || !eva.isActive").move__item <i class="fas fa-users"></i> В подруги
                li(@click="setStatus", data-status="favorite", v-if="eva.status!='favorite' && eva.status!='aspirant' && eva.status!='winner' || !eva.isActive").move__item <i class="fas fa-praying-hands"></i> В избранные
</template>

<script>
    import { mapActions } from 'vuex';
    export default {
      name: 'move',
      props: {
        eva: Object
      },
      methods: {
        ...mapActions(['moveEva', 'moveDarkEva']),
        setStatus(e){
          var eva = this.eva;
          eva.move = e.target.getAttribute('data-status');
          this.$nextTick(function () {
            if(eva.isActive){
              this.moveEva(eva);
            } else {
              eva.isActive = true;
              this.moveDarkEva(eva);
            }
          });
        }
      }
    }
</script>
<style lang="scss" src="../assets/styles/elements/penalty.scss"></style>


