<template lang="pug">
    ul.penalty
        li.prenalty__item.penalty__opener <i class="fas fa-exclamation-triangle"></i> Штрафы ({{penalty}}) 
            ul.penalty__menu
                li.penalty__item(:data-id="id", data-penalty="0", @click="setPenalty") 0
                li.penalty__item(:data-id="id", data-penalty="1", @click="setPenalty") 1
                li.penalty__item(:data-id="id", data-penalty="2", @click="setPenalty") 2
                li.penalty__item(:data-id="id", data-penalty="3", @click="setPenalty") 3
</template>

<script>
    import { mapActions } from 'vuex';
    export default {
      name: 'penalty',
      props: {
        penalty: String,
        id: String
      },
      methods: {
        ...mapActions(['strikeEva']),
        setPenalty(e){
          var eva = {
            id: this.id,
            penaltyNumber: e.target.getAttribute('data-penalty')
          }
          this.$nextTick(function () {
              this.strikeEva(eva);
          });
        }
      }
    }
</script>

<style lang="scss" src="../assets/styles/elements/penalty.scss"></style>


