<template lang="pug">
    .archive(:class="loaderClass")
        .preloader__spinner
            .preloader__animation
        .archive__item(v-for="eva in dark_evas", v-bind:key="eva.id")
            dark_eva(:eva="eva")
</template>

<script>
    import dark_eva from './dark-eva';
    import { mapState, mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    export default {
      name: 'archive',
      data () {
        return {
          loaderClass: 'archive--loading'
        }
      },
      computed: {
        ...mapState({
            dark_evas: state => state.dark_evas.dark_evas
        })
      },
      methods: {
         ...mapActions(['getAllDarkEvasFromApi'])
      },
      mounted(){
        this.$nextTick(function () {
            this.getAllDarkEvasFromApi();
        });
        eventBus.$on('loadPage', data => {
          this.loaderClass = data.loaderClass;
        }); 
      },
      components: {
        dark_eva
      }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .archive{
        display:grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 20px;
        .preloader__spinner{
            display: none;
        }
    }
    .archive--loading{
        background-color: transparent;
        .preloader__spinner{
            display: block;
        }
        .archive__item{
            opacity:0;
            z-index:-2;
        }
    } 
</style>
<style lang="scss" src="../assets/styles/elements/preloader.scss"></style>


