<template lang="pug">
    .eva-form(v-if="visible")
       .eva-form__overlay(@click="closeThisWindow")
       .eva-form__content 
            <svg @click="closeThisWindow" class="close-icon close-icon--light" xmlns="http://www.w3.org/2000/svg" width="15" height="16" viewBox="0 0 15 16"><g><g><path class="close-icon close-icon--light" fill="#163850" d="M14.218 1.99a2 2 0 0 1 0 2.828L11.036 8l3.182 3.182a2 2 0 0 1 0 2.828l-.708.708a2 2 0 0 1-2.828 0L7.5 11.536l-3.182 3.182a2 2 0 0 1-2.828 0l-.708-.708a2 2 0 0 1 0-2.828L3.964 8 .782 4.818a2 2 0 0 1 0-2.828l.708-.708a2 2 0 0 1 2.828 0L7.5 4.464l3.182-3.182a2 2 0 0 1 2.828 0z"/></g></g></svg>
            form.eva-form__sending-form
                .eva-form__item
                    legend.eva-form__title {{ title }}
                .eva-form__item
                    img.eva-form__image(:src="eva.image")
                .eva-form__item
                    input.eva-form__input#inputFile.hide(type="file", @change="selectFile")
                    input.eva-form__input.hide(type="text", v-model="eva.image")
                    button.eva-form__submit(@click="clickInputfile") Добавить фото
                .eva-form__item
                    label.eva-form__label Имя
                    input.eva-form__input(type="text", placeholder="Имя", v-model="eva.name")
                
                .eva-form__item(v-if="eva.status != 'favorite' && eva.status != 'winner' && eva.isActive")
                    label.eva-form__label Внешность 
                    input.eva-form__input(type="number", placeholder="Внешность", v-model="eva.appearance")
                .eva-form__item(v-else-if="!eva.isActive")
                    label.eva-form__label Внешность 
                    input.eva-form__input(type="number", placeholder="Внешность", v-model="eva.appearance")
                
                .eva-form__item(v-if="eva.status != 'favorite' && eva.status != 'winner' && eva.isActive")
                    label.eva-form__label Индивидуальность 
                    input.eva-form__input(type="number", placeholder="Индивидуальность", v-model="eva.mind")
                .eva-form__item(v-else-if="!eva.isActive")
                    label.eva-form__label Индивидуальность 
                    input.eva-form__input(type="number", placeholder="Индивидуальность", v-model="eva.mind")
                
                .eva-form__item
                    label.eva-form__label Описание 
                    textarea.eva-form__textarea(placeholder="Описание", v-model="eva.text")
                button.eva-form__submit(@click="uploadEva") Сохранить
</template>

<script>
    import {eventBus} from '../main.js';
    import { mapActions } from 'vuex';
    export default {
      name: 'evaform',
      data(){
          return {
              visible: false,
              title: 'Добавить Еву',
              isNewEva: true,
              eva: {}
          }
      },
      methods: {
        ...mapActions(['postNewEva', 'updateEva', 'updateDarkEva']),
        closeThisWindow(){
            this.visible = false;
        },
        clickInputfile(e){
            e.preventDefault();
            document.getElementById('inputFile').click();
        },
        selectFile(e){
            var file = e.target.files[0];
            var reader = new FileReader();
            var eva = this.eva;
            reader.onload = function () {
                var isValidType = (file.type == 'image/png'
                    || file.type == 'image/jpeg'
                    || file.type == 'image/jpg');
                var isValidSize = file.size / 1024 / 1024 < 2;
                if(isValidType && isValidSize){
                    eva.image = reader.result;
                    eva.file = file;
                } else if(!isValidType) {
                    document.getElementById('inputFile').value = '';
                    eventBus.$emit('showStatus', {text: 'Можно загружать только изображения форматов: png, jpeg, jpg', visible: true, class: 'm-fail'});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''})
                    }, 3000);
                } else if(!isValidSize){
                    document.getElementById('inputFile').value = '';
                    eventBus.$emit('showStatus', {text: 'Файл не должен привышать размер 2mb', visible: true, class: 'm-fail'});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''})
                    }, 3000);
                }
                
            };
            reader.readAsDataURL(file);
        },
        uploadEva(e){
            e.preventDefault();
            var eva = this.eva;
            this.$nextTick(function () {
                if(this.isNewEva){
                    this.postNewEva(eva);
                } else if(eva.isActive){
                    this.updateEva(eva);
                } else {
                    this.updateDarkEva(eva);
                }
            }) 
        }
      },
      mounted(){
        eventBus.$on('openingEvaform', data => {
          this.visible = data.visible;
          this.title = data.title;
          this.eva = data.eva;
          this.isNewEva = data.isNewEva;
        });
      }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';
    @mixin placeholder {
        &::-webkit-input-placeholder { @content; }
        &:-moz-placeholder           { @content; }
        &::-moz-placeholder          { @content; }
        &:-ms-input-placeholder      { @content; }  
    }
    .eva-form{
        position: fixed;
        width: 100vw;
        height: 100vh;
        z-index: 9000;
        top:0;
        left:0;
        right:0;
        bottom:0;
        display:flex;
    }
    .eva-form__overlay{
        width:100%;
        height: 100%;
        background-color: rgba($mainColor, 0.8);
        position:absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
    }
    .eva-form__content{
        width: 700px;
        min-height:500px;
        max-height: 100%;
        overflow:auto;
        border-radius: 4px;
        background-color: $mainColor;
        padding: 30px 35px;
        margin: auto;
        position:relative;
    }  
    .eva-form__title{
        color: $white;
        font-size: $textFontSize;
        font-weight: 600;
        text-transform: uppercase;
    }
    .eva-form__item{
        margin-bottom: 20px;
    }
    .eva-form__image{
        max-width:150px;
        height:auto;
        display:block;
        margin: 0 auto;
        border-radius: 50%;
    }
    .eva-form__submit{
        width: 100%;
        height: 50px;
        border-radius: 3px;
        background-color: $accentColor;
        border: 2px solid $accentColor;
        color: $white;
        font-size: $h6fontSize;
        font-weight: 600;
        letter-spacing: 2px;
        text-transform: uppercase;
        transition: all .3s;
        max-width:332px;
        margin: 0 auto;
        display:block;
        &:hover{
            color: $accentColor;
            background-color: transparent;
        }
    }
    .eva-form__input{
        width: 100%;
        height: 52px;
        border-radius: 4px;
        border: 1px solid $white;
        padding-left:20px;
        background: transparent;
        outline:none;
        color: $white;
        font-size: $textFontSize;
        font-weight: 500;
        font-family: $mainFont;
        transition: all .3s;
        @include placeholder{
            color: $accentColor;
        }
        &:focus{
            box-shadow: 0 0 4px $accentColor;
            border: 1px solid $accentColor;
        }
    }
    .eva-form__textarea{
        width: 100%;
        height: 200px;
        border-radius: 4px;
        border: 1px solid $white;
        padding:20px;
        background: transparent;
        outline:none;
        color: $white;
        font-size: $textFontSize;
        font-weight: 500;
        resize: vertical;
        font-family: $mainFont;
        transition: all .3s;
        @include placeholder{
            color: $accentColor;
        }
        &:focus{
            box-shadow: 0 0 4px $accentColor;
            border: 1px solid $accentColor;
        }
    }
    .eva-form__label{
        color:#fff;
    }
    .close-icon--light.close-icon--light{
        fill: white;
        &:hover{
            fill: $accentColor;
        }
    }
</style>


