<template lang="pug">
include ../templates/button
.eva(:id="eva.id")
    .eva__id {{ eva.id }}
    .eva__avatar   
        img(:src="eva.image")
    .eva__name {{ eva.name }}
    .eva__charact(v-if="eva.status!='favorite' && eva.status!='winner'")
        .eva__title Внешность
        .eva__raitng(:class="'eva__raitng--' + eva.appearance")
            .eva__star 
                <i class="fas fa-star"></i>
            .eva__star 
                <i class="fas fa-star"></i>
            .eva__star 
                <i class="fas fa-star"></i>
            .eva__star 
                <i class="fas fa-star"></i>
            .eva__star 
                <i class="fas fa-star"></i>
    .eva__charact(v-if="eva.status!='favorite' && eva.status!='winner'")
        .eva__title Индивидуальность
        .eva__raitng(:class="'eva__raitng--' + eva.mind")
            .eva__star 
                <i class="fas fa-star"></i>
            .eva__star 
                <i class="fas fa-star"></i>
            .eva__star 
                <i class="fas fa-star"></i>
            .eva__star 
                <i class="fas fa-star"></i>
            .eva__star 
                <i class="fas fa-star"></i>
    .eva__penaltys
        penalty(:penalty="eva.penalty", :id="eva.id")
    .eva__control-item
        move(:eva="eva")
    .eva__title Описание
    .eva__text(v-html="eva.text")
    a(href="#", @click="toggleDescWindow").eva__text-more Подробнее
    .eva__control-item
        edit_eva_btn(:eva="eva")
    .eva__control-item(v-if="eva.status == 'favorite'")
        winner_btn(:eva="eva", :winners="winners")
    .eva__control-item
        archive_btn(:eva="eva")
</template>

<script>
    import edit_eva_btn from './edit-eva';
    import winner_btn from './winner-btn';
    import archive_btn from './archive-eva';
    import penalty from './penalty';
    import move from './move';
    import {eventBus} from '../main.js';
    export default {
      name: 'eva',
      props: {
        eva: Object,
        winners: Number
      },
      components: {
        edit_eva_btn,
        winner_btn,
        archive_btn,
        penalty,
        move
      },
      methods: {
          toggleDescWindow(event) {
              event.preventDefault();
              eventBus.$emit('callDescWindow', {visible: true, text: this.eva.text});
          }
      }
    }
</script>

<style lang="scss" src="../assets/styles/elements/button.scss"></style>
<style lang="scss" src="../assets/styles/elements/move.scss"></style>
<style lang="scss" src="../assets/styles/elements/penalty.scss"></style>
<style lang="scss">
    @import '../assets/styles/layout/vars.scss';    
    .eva{
        border-radius: 8px;
        background-color: $white;
        border: 2px solid $accentColor;
        padding: 15px;
        margin-bottom: 15px;
    }

    .dark-eva{
        background-color: $mainColor;
        color: $white;
        .eva__star{
            color: $white;
        }
        .eva__raitng--1{
            .eva__star:first-child{
                color: $accentColor;
            }
        }

        .eva__raitng--2{
            .eva__star:first-child, .eva__star:nth-child(2){
                color: $accentColor;
            }
        }

        .eva__raitng--3{
            .eva__star{
                color: $accentColor;
            }
            .eva__star:last-child, .eva__star:nth-child(4){
                color: $white;
            }
        }

        .eva__raitng--4{
            .eva__star{
                color: $accentColor;
            }
            .eva__star:last-child{
                color: $white;
            }
        }

        .eva__raitng--5{
            .eva__star{
                color: $accentColor;
            }
        }
        .move__item.move__opener{
            border:2px solid $white;
        }
        .move__menu{
            border:2px solid $white;
            border-top:none;
        }
        .eva__text-more{
            color: $white;
        }
    }

    .eva__id{
        width: 100%;
        text-align: center;
        margin-bottom: 15px;
        font-family: $mainFont;
        font-size: 12px;
        font-weight: 400;
    }

    .eva__avatar{
        width: 150px;
        height: 150px;
        margin: 0 auto 15px;
        border-radius:50%;
        overflow: hidden;
    }

    .eva__name{
        font-size: $h3fontSize;
        font-weight: 700;  
        margin-bottom: 20px; 
        text-align: center;
    }

    .eva__title{
        font-size: $h5fontSize;
        font-weight: 700;
    }

    .eva__text-more{
        font-size: $h6fontSize;
        font-weight: 700;
        color: $mainColor;
        &:hover{
            text-decoration: none;
        }
    }

    .eva__star{
        font-size: $h3fontSize;
        color: $mainColor;
        transition: all .3s;
    }

    .eva__control-item{
        margin: 20px 0;
    }

    .eva__text{
        max-height: 170px;
        overflow: hidden;
        word-break: break-all;
    }

    .eva__raitng{
        display: flex;
        justify-content: space-between;
        margin: 15px 0;
    }

    .eva__raitng--1{
        .eva__star:first-child{
            color: $accentColor;
        }
    }

    .eva__raitng--2{
        .eva__star:first-child, .eva__star:nth-child(2){
            color: $accentColor;
        }
    }

    .eva__raitng--3{
        .eva__star{
            color: $accentColor;
        }
        .eva__star:last-child, .eva__star:nth-child(4){
            color: $mainColor;
        }
    }

    .eva__raitng--4{
        .eva__star{
            color: $accentColor;
        }
        .eva__star:last-child{
            color: $mainColor;
        }
    }

    .eva__raitng--5{
        .eva__star{
            color: $accentColor;
        }
    }
</style>


