<template lang="pug">
    .window-description(v-if="visible")
       .window-description__overlay(@click="closeThisWindow")
       .window-description__content 
        <svg @click="closeThisWindow" class="close-icon" xmlns="http://www.w3.org/2000/svg" width="15" height="16" viewBox="0 0 15 16"><g><g><path class="close-icon" fill="#163850" d="M14.218 1.99a2 2 0 0 1 0 2.828L11.036 8l3.182 3.182a2 2 0 0 1 0 2.828l-.708.708a2 2 0 0 1-2.828 0L7.5 11.536l-3.182 3.182a2 2 0 0 1-2.828 0l-.708-.708a2 2 0 0 1 0-2.828L3.964 8 .782 4.818a2 2 0 0 1 0-2.828l.708-.708a2 2 0 0 1 2.828 0L7.5 4.464l3.182-3.182a2 2 0 0 1 2.828 0z"/></g></g></svg>
        div(v-html="text")        
</template>

<script>
    import {eventBus} from '../main.js';
    export default {
      name: 'windowDesc',
      data(){
          return {
              text: 'Какой-то текст по умолчанию',
              visible: false
          }
      },
      methods: {
          closeThisWindow(){
              this.visible = false;
          }
      },
      mounted(){
        eventBus.$on('callDescWindow', data => {
          this.visible = data.visible;
          this.text = data.text;
        });
      }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .window-description{
        position: fixed;
        width: 100vw;
        height: 100vh;
        z-index: 9000;
        top:0;
        left:0;
        right:0;
        bottom:0;
        display:flex;
    }
    .window-description__overlay{
        width:100%;
        height: 100%;
        background-color: rgba($mainColor, 0.8);
        position:absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
    }
    .window-description__content{
        width:40%;
        min-height:500px;
        max-height: 100%;
        overflow:auto;
        border-radius: 4px;
        background-color: $white;
        padding: 35px 40px;
        margin: auto;
        position:relative;
    }

    .close-icon{
        position:absolute;
        top: 20px;
        right: 20px;
        width:20px;
        height:20px;
        cursor:pointer;
        &:hover{
            fill: $accentColor;
        }
    }
</style>


